@import "grid-flex";

/*variables*/

$blue: #17529F;
$black: #1C1C1C;
$green: #4BAF57;
$text_gray: #666666;
$section_gray: #F9F9F9;
$header_gray: #9E9E9E;

/*end variables*/

/*reset css*/
* {
  box-sizing: border-box;
}

body {
  font-family: "Inter";
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  color: $text_gray;
}

p, ul, li, h1, h2, h3, html, body, button {
  margin: 0;
  padding: 0;
}

a {
  color: #1C1C1C;
  text-decoration: none;
}

button {
  outline: none;
  border: none;
  background: none;
  cursor: pointer;
}

/*end reset css*/

/*common styles*/

.container {
  &--white {
    padding-top: 120px;
    padding-bottom: 120px;
  }

  &--grey {
    padding-top: 50px;
    padding-bottom: 100px;
    background-color: $section_gray;
  }
}

.default-input {
  opacity: 0;
  z-index: -9999;
  position: absolute;
  visibility: hidden;
}

.section {
  max-width: 1430px;
  margin: 0 auto;

  &-title {
    font-family: "Inter";
    font-style: normal;
    font-weight: 600;
    font-size: 46px;
    line-height: 120%;
    margin-bottom: 30px;

    span:first-child {
      color: $blue;
      display: block;
    }

    span:nth-child(2) {
      color: $header_gray;
    }
  }
}

.content {
  padding-left: 100px;

  &--wide {
    padding-left: 0;
  }
}

.main-btn {
  width: 250px;
  height: 50px;
  background: $blue;
  border-radius: 3px;
  font-family: "Inter";
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.02em;
  color: #FFFFFF;
  text-transform: uppercase;
  &:hover{
    background: darken($blue,5%);
  }

  &--whatsapp {
    background: white;
    color: $green;
    border: 1px solid $green;
    &:hover{
      background: $green;
      color: white;
    }
  }

  &--transparent-white {
    background: transparent;
    color: white;
    border: 1px solid white;
    &:hover{
      background: $blue;
      color: white;
    }
  }

  &--transparent-blue {
    background: transparent;
    color: $blue;
    border: 1px solid $blue;
    &:hover{
      background: $blue;
      color: white;
    }
  }
}

.arrows-block {
  position: absolute;
  display: flex;
  right: 15px;
  top: 0;
  width: 85px;
  justify-content: space-between;

  .button-prev svg {
    transform: rotate(-180deg);
  }
}

/*common styles*/

.main-header {
  background: white;
  height: 100px;

  &__wrapper {
    background: white;
    height: 100%;
    max-width: 1430px;
    padding: 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__phone {
    margin-right: 30px;
    margin-left: auto;
  }

  &__whatsapp {
    margin-right: 30px;
  }

  &__logo {
    margin-right: 30px;
  }

  &__menu-btn {
    font-family: "Inter";
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;
    text-transform: uppercase;
    color: $black;
  }

  &__phone {
    font-family: "Inter";
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
  }
}

.main-screen {
  height: 700px;
  background: url("../images/main-screen.jpg") no-repeat center;
  background-size: cover;
  max-width: 1920px;
  margin: 0 auto;

  &__wrapper {
    max-width: 1430px;
    padding: 0 15px;
    margin: 0 auto;
    border-top: 1px solid transparent;
  }

  &__title {
    font-family: "Inter";
    font-style: normal;
    font-weight: 600;
    font-size: 80px;
    line-height: 110%;
    color: #FFFFFF;
    margin-top: 130px;

    span {
      display: block;
      padding-left: 130px;
    }
  }

  &__subtitle {
    font-family: "Inter";
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 150%;
    color: #FFFFFF;
    padding-left: 130px;
    margin: 30px 0 40px 0;
  }

  &__btn {
    margin-left: 130px;
    margin-right: 30px;
  }
}

.description {

  &__text {
    max-width: 720px;
    color: $black;
    margin-bottom: 50px;
  }

  &__numbers {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }

  &__numbers-item {
    list-style: none;
  }

  &__number {
    display: block;
    text-align: center;
    font-family: "Inter";
    font-style: normal;
    font-weight: normal;
    font-size: 60px;
    color: $blue;
    margin-bottom: 10px;
  }

  &__numbers-descr {
    font-size: 16px;
    color: $black;
  }
}

.sro {

  &__title {
    font-style: normal;
    font-weight: 500;
    font-size: 30px;
    line-height: 120%;
    color: $black;
    margin-bottom: 20px;
  }

  &__description {
    font-family: "Inter";
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 140%;
    color: $text_gray;
  }

  &__item {
    background: #FFFFFF;
    max-width: 1100px;
    padding: 30px 6% 50px 30px;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.05);

    &:not(:last-child) {
      margin-bottom: 35px;
    }
  }

  &__br-bold {
    display: block;
    font-family: "Inter";
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    color: $black;
    margin-bottom: 10px;
  }

  &__wrapper > * {
    flex-basis: 30%;
  }

  &__wrapper {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  &__get-sro {
    margin-bottom: 20px;
  }

  &__download {
    padding-top: 5px;
  }

  &__download svg {
    float: left;
    margin-right: 10px;
  }

  &__offer {
    color: $black;
    font-family: Inter;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
  }

  &__get-offer {
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: $text_gray;
  }

  button {
    max-width: 285px;
  }
}

.calculate {

  max-width: 1350px;

  &__header {
    font-style: normal;
    display: flex;
    font-weight: 600;
    font-size: 20px;
    line-height: 130%;
    color: $black;
    margin-bottom: 15px;
  }

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: space-between;
  }

  &__number-border {
    border: 1px solid $blue;
    width: 25px;
    height: 25px;
    display: flex;
    align-items: center;
    text-align: center;
    transform: rotate(45deg);
    position: absolute;
    left: 0;
  }

  &__number {
    transform: rotate(-45deg);
    display: block;
    margin: 0 auto;
    text-align: center;
    color: $blue;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 120%;
  }

  &__label {
    display: block;
  }

  &__question {
    position: relative;
    padding-left: 40px;
    flex-basis: 33%;

    &:nth-child(3) {
      margin-bottom: 55px;
    }
  }

  &__text-input {
    width: 260px;
    height: 50px;
    border: 1px solid #FFFFFF;
    border-radius: 3px;
    background: transparent;
    color: white;
    padding-left: 15px;
    outline: none;

    &::placeholder {
      font-family: "Inter";
      font-style: normal;
      font-weight: normal;
      font-size: 15px;
      line-height: 18px;
      color: #FFFFFF;
    }

    &::-webkit-input-placeholder {
      font-family: "Inter";
      font-style: normal;
      font-weight: normal;
      font-size: 15px;
      line-height: 18px;
      color: #FFFFFF;
    }

    &::-moz-placeholder {
      font-family: "Inter";
      font-style: normal;
      font-weight: normal;
      font-size: 15px;
      line-height: 18px;
      color: #FFFFFF;
    }

    &:-ms-input-placeholder {
      font-family: "Inter";
      font-style: normal;
      font-weight: normal;
      font-size: 15px;
      line-height: 18px;
      color: #FFFFFF;
    }

    &::-moz-placeholder {
      font-family: "Inter";
      font-style: normal;
      font-weight: normal;
      font-size: 15px;
      line-height: 18px;
      color: #FFFFFF;
    }
  }

  &__submit {
    width: 280px;
    height: 50px;
    color: $blue;
    background: #FFFFFF;
    &:hover{
      background: darken(white,5%);
      color: $blue;
    }
  }


  &__callback {
    background: $blue;
    padding: 35px 50px 45px 50px;
    margin-top: 30px;
  }

  &__callback-title {
    font-family: "Inter";
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
    color: #FFFFFF;
  }

  &__callback-subtitle {
    font-family: "Inter";
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 140%;
    color: #FFFFFF;
    margin-top: 10px;
    margin-bottom: 20px;
    max-width: 385px;
  }

  &__inputs-block {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__total {
    outline: none;
    width: 285px;
    height: 40px;
    border: 1px solid $blue;
    display: block;
    padding-left: 15px;
  }
}

.custom-input {
  line-height: 25px;

  &__btn {
    width: 20px;
    display: inline-block;
    height: 20px;
    border: 1px solid $text_gray;
    position: relative;
    margin-right: 10px;
    vertical-align: middle;

    &--radio {
      border-radius: 100%;
    }
  }
}


.default-input:checked + .custom-input .custom-input__btn {
  &:after {
    content: "";
    display: block;
    width: 10px;
    height: 10px;
    background-color: $blue;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
  }
}

.default-input:checked + .custom-input .custom-input__btn--radio {
  border-color: $blue;

  &:after {
    border-radius: 100%;

  }
}

.swiper-button-next, .swiper-button-prev {

}

.team {
  height: 710px;
  position: relative;
  max-width: 1920px;
  margin: 0 auto;
  padding: 50px 15px 0 15px;

  &__slide {
    width: 320px;
    height: 450px;
    margin: 45px 0 0 auto;
  }

  &__slide-title {
    font-family: "Inter";
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 29px;
    color: $black;
    display: none;
  }

  &__slide-subtitle {
    font-family: "Inter";
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 140%;
    color: $text_gray;
    display: none;
  }

  &__btn {
    display: none;
  }

  &__cite {
    position: absolute;
    max-width: 460px;
    bottom: 55px;
    left: 450px;
  }

  &__cite svg {
    position: absolute;
    top: -15px;
    left: -15px;
  }

  //&__arrows-block{
  //  position: absolute;
  //  display: flex;
  //  right: 15px;
  //  top: 0;
  //  width: 85px;
  //  justify-content: space-between;
  //
  //  .button-prev svg{
  //    transform: rotate(-180deg);
  //  }
  //}

  .swiper-slide-active .team__slide {
    height: 550px;
    width: 400px;
    transition: 0.4s ease all;
    margin-top: 0;
  }

  .swiper-slide-active .team__slide img {
    filter: none;
  }

  .swiper-slide-active .team__slide-subtitle,
  .swiper-slide-active .team__btn,
  .swiper-slide-active .team__slide-title {
    display: block;
  }

  .swiper-slide img {
    width: auto;
    height: auto;
    max-width: 100%;
    max-height: 100%;
    filter: brightness(50%);
  }
}

.about {
  &__wrapper {
    display: flex;
    flex-wrap: wrap;
