  }
}

.compare {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;

  &__title {
    font-family: Inter;
    font-style: normal;
    font-weight: 500;
    font-size: 32px;
    line-height: 120%;
    color: $black;
