  &__list-item-info {
    font-family: Inter;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 150%;
    color: #676767;
    max-width: 60%;
  }

  &__list--blue {
    border-bottom: 1px solid #4575B2;
    background: $blue;

    * {
      color: white;
    }
