  &__list-item {
    position: relative;
    list-style: none;
  }

  &__number {
    display: block;
    position: absolute;
    left: -90px;
    top: 0;
    color: $blue;
    border: 1px solid rgba(23, 82, 159, 0.2);
    border-radius: 100%;
    width: 60px;
    height: 60px;
    text-align: center;
    line-height: 60px;
    font-family: Inter;
    font-style: normal;
    z-index: 2;
    background: white;
    font-weight: normal;
    font-size: 24px;
  }

  &__title {
    font-family: Inter;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 29px;
    color: $black;
    margin-bottom: 15px;
