.popup{
  justify-content: space-between;
  width: 100vw;
  height: 100vh;
  background: white;
  position: fixed;
  left: 0;
  top: 0;
  display: none;
  z-index: 3;

  &__wrapper{
    display: flex;
    width: 100%;
    height: 100%;
  }

  &__content{
    background: $section_gray;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    width: 50%;
    padding:0 100px;
    z-index: 2;
  }

  &__input{
    width: 600px;
    max-width: 100%;
    display: block;
    margin-top: 30px;
    height: 80px;
    background: white;
    border:none;
    outline: none;
    font-family: Inter;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 24px;
    padding-left: 30px;
  }

  &__personal{
    font-family: Inter;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    color: #313131;
    display: block;
    margin-top: 35px;

    a{
      text-decoration: underline;
    }
  }

  &__btn{
    width: 100%;
    margin-top: 30px;
  }

  &__cross{
    position: absolute;
    right: 30px;
    top: 30px;
  }



  &__title{
    font-family: Inter;
    font-style: normal;
    font-weight: 500;
    font-size: 36px;
    line-height: 44px;
    color: $black;
  }

  &__background{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    svg{
      transition: 300ms ease-in-out all;
      width: 100%;
      opacity: 0;
      z-index: 2;
    }

    svg.opened{
      width: 400px;
      transition: 0.5s ease-in-out all;
      opacity: 1;
    }
  }
  &__background-img{
    background: url("../images/menu-theme.jpg") no-repeat;
    filter: blur(5px);
    background-size: cover;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
  }

  &--menu{
    .page-menu{
      list-style: none;

      &__item{
        margin-bottom: 20px;
      }

      &__link{
        font-family: Inter;
        font-style: normal;
        font-weight: 500;
        font-size: 34px;
        line-height: 140%;
        color: $black;
      }
    }

    .popup__background-img{
      background: url("../images/menu-theme3.jpg") no-repeat;
      background-size: cover;
    }
  }
}











